%h1 {
  font-family: "Playfair Display", serif;
  font-weight: 700;
  font-size: 48px;
  line-height: 100%;
  color: $black-color;

  @include onTablet {
    font-size: 72px;
  }

  @include onDesktop {
    font-size: 96px;
  }
}

%h2 {
  font-family: "Playfair Display", serif;
  font-weight: 700;
  font-size: 38px;
  line-height: 100%;
  color: $black-color;

  @include onTablet {
    font-size: 62px;
  }

  @include onDesktop {
    font-size: 80px;
  }
}

%h3 {
  font-family: "Playfair Display", serif;
  font-weight: 700;
  font-size: 28px;
  line-height: 125%;
  color: $black-color;

  @include onTablet {
    font-size: 40px;
  }

  @include onDesktop {
    font-size: 58px;
  }
}

%h4 {
  font-family: "Playfair Display", serif;
  font-weight: 700;
  font-size: 20px;
  line-height: 125%;
  color: $black-color;

  @include onTablet {
    font-size: 24px;
  }
}

%h5 {
  font-family: "Playfair Display", serif;
  font-weight: 700;
  font-size: 20px;
  line-height: 150%;
  color: $black-color;
}

%h6 {
  font-family: "Playfair Display", serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 150%;
  color: $black-color;
}

%p {
  font-family: "Raleway", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: $black-color;
}
